@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
#editor {
	width: 100%;
	height: 100%;
	position: fixed;
	overflow: hidden;
}

#editor-menu {
	position: fixed;
	z-index: 9999;
	width: 100%;
	display: inline-block;
	background-color: #242424;
	padding-bottom: 10px;
	padding-top: 10px;
	box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 1);
}

#editor-menu>h1 {
	color: white;
	margin: 0;
	padding: 0;
	font-size: 21px;
	position: fixed;
	top: 10px;
	right: 20px;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

#editor-menu>div {
	display: inline-block;
	margin-left: 10px;
}

#editor-menu>div>button {
	background-color: #494949;
	color: white;
	border: none;
	padding: 5px;
	margin-right: 5px;
	width: 100px;
	cursor: pointer;
	font-size: 15px;
	font-family: 'Roboto', sans-serif;
}
.node-menu-category {
	margin-top: 2px;
}

.node-menu-category>.header {
	height: 30px;
	background-color: rgba(0, 0, 0, 0.75);
}

.node-menu-category>.header>button {
	background: none;
	border: none;
	margin-right: 5px;
	color: white;
	float: right;
	font-weight: bolder;
	font-size: 20px;
	pointer-events: none;
}

.node-menu-category>.header>span {
	color: white;
	position: relative;
	top: 4px;
	right: -10px;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.node-menu-category>.content {
	overflow: hidden;
	margin-left: 10px;
}
.node-menu-item {
	color: white;
	padding: 2px;
}

.node-menu-item>div {
	background-color: rgba(50, 50, 50, 0.5);
	padding: 2px;
	padding-left: 5px;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}
#node-selector-menu {
	width: 250px;
	background-color: rgba(0, 0, 0, 0.5);
	display: flex;
	flex-flow: column;
	height: 100%;
	border-right: solid 1px #494949;
	position: fixed;
	top: 45px;
	z-index: 9998;
}

#node-selector-menu>input {
	margin: 5px;
	margin-top: 10px;
	background-color: rgba(0, 0, 0, 0.75);
	border: solid 2px rgba(0, 0, 0, 0.75);
	color: white;
	padding: 5px;
	font-size: 16px;
}

#node-selector-menu>input:focus {
	border: solid 2px #494949;
}

#node-selector-menu>hr {
	margin: 0;
	width: 100%;
	margin-top: 5px;
	margin-left: -2px;
	margin-bottom: 10px;
	color: #494949;
}
body {
	background-color: #282828;
}

#graph {
	position: fixed;
	background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAD0AAAA8BAMAAAAnEK9/AAAAAXNSR0IArs4c6QAAAARnQU1BAACxjwv8YQUAAAAGUExURVFRUTMzM3nhs84AAAAJcEhZcwAADsMAAA7DAcdvqGQAAAAhSURBVEjHY6AYCOIBAqPyo/Kj8qPyo/Kj8sNUnhLAwAAAui1roY+vV8MAAAAASUVORK5CYII=);
	background-repeat: repeat;
	background-size: 15px;
	border: 4px solid #555555;
}

#graph>canvas {
	width: 100%;
	height: 100%;
}
#graph-controls {
	position: absolute;
	bottom: 0;
	right: 0;
	z-index: 9999;
	background-color: rgba(0, 0, 0, 0.5);
	padding-bottom: 5px;
	padding-top: 6px;
	padding-right: 10px;
	padding-left: 5px;
}

#graph-controls>input {
	border: none;
	background-color: rgba(0, 0, 0, 0.75);
	border: solid 2px rgba(0, 0, 0, 0.75);
	color: white;
	padding: 2px;
	font-size: 11px;
	margin-bottom: 2px;
}

#graph-controls>label {
	color: white;
	font-size: 11px;
	font-weight: bolder;
	margin-left: 10px;
	margin-right: 5px;
	margin-bottom: 2px;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

#graph-controls>button {
	color: white;
	background-color: rgba(0, 0, 0, 0.5);
	border: none;
	font-size: 20px;
	font-weight: bolder;
	position: fixed;
	bottom: 35px;
	right: 0px;
}
.graph-node {
	height: 200px;
	color: white;
	box-shadow: 0px 0px 6px 0px #000000;
}

.graph-node>.header {
	padding: 2px;
}

.graph-node>.header>button {
	position: absolute;
	top: 4px;
	right: 4px;
	background: none;
	border: 1px solid transparent;
	color: white;
	font-weight: bolder;
}

.graph-node>.content {
	background-color: rgba(0, 0, 0, 0.75);
	height: 175px;
}

.graph-node>.header>div {
	background: rgba(0, 0, 0, 0.3);
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	font-size: 14px;
	text-align: left;
	padding-left: 6px;
	padding-bottom: 2px;
	padding-top: 2px;
}
html, body, div {
	margin: 0;
	padding: 0;
	font-family: 'Roboto', sans-serif;
}
