#editor {
	width: 100%;
	height: 100%;
	position: fixed;
	overflow: hidden;
}

#editor-menu {
	position: fixed;
	z-index: 9999;
	width: 100%;
	display: inline-block;
	background-color: #242424;
	padding-bottom: 10px;
	padding-top: 10px;
	box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 1);
}

#editor-menu>h1 {
	color: white;
	margin: 0;
	padding: 0;
	font-size: 21px;
	position: fixed;
	top: 10px;
	right: 20px;
	user-select: none;
}

#editor-menu>div {
	display: inline-block;
	margin-left: 10px;
}

#editor-menu>div>button {
	background-color: #494949;
	color: white;
	border: none;
	padding: 5px;
	margin-right: 5px;
	width: 100px;
	cursor: pointer;
	font-size: 15px;
	font-family: 'Roboto', sans-serif;
}